@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,300;0,400;0,800;1,300;1,400;1,500;1,600;1,700;1,800&display=swap');
$primaryColor: #4527a4;

.interface-interface-skeleton__header {
	z-index: 100;
}

.bBlocksTemplateLibraryWrap {
	* {
		margin: 0;
		box-sizing: border-box;
	}

	.libraryBtn {
		font-size: 13px;
		letter-spacing: 0.5px;
		color: $primaryColor;
		background-color: transparent;
		display: flex;
		align-items: center;
		gap: 5px;
		padding: 12px 10px;
		border: none;
		border-radius: 5px;
		cursor: pointer;

		&.active {
			font-weight: 500;
			color: white;
			background-color: $primaryColor;
		}
	}

	.templateLibraryButton {
		font-size: 13px;
		line-height: 120%;
		font-weight: 500;
		color: $primaryColor;
		background-color: #0000;
		display: flex;
		align-items: center;
		gap: 5px;
		padding: 6px 15px;
		margin: 0 5px;
		border: 1px solid $primaryColor;
		border-radius: 4px;
		transition: all 0.3s ease-in-out;
		cursor: pointer;

		img {
			width: 24px;
			height: 24px;
		}
	}

	.templateLibraryModalWrap {
		width: 100%;
		height: 100dvh;
		backdrop-filter: blur(3px);
		display: flex;
		align-items: center;
		justify-content: center;
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		z-index: 9999;

		::-webkit-scrollbar {
			width: 10px;
		}

		::-webkit-scrollbar-track {
			box-shadow: inset 0 0 5px $primaryColor;
			border-radius: 10px;
		}

		::-webkit-scrollbar-thumb {
			background: $primaryColor;
			border-radius: 10px;
		}

		.modalContainer {
			width: 90dvw;
			height: 80dvh;
			border-radius: 10px;
			box-shadow: 0px 0px 10px 8px #b5b5b573;
			background-color: white;

			.modalHeader {
				display: flex;
				align-items: center;
				justify-content: space-between;
				border-bottom: 1px solid #cecece;
				padding: 20px 30px;

				.modalHeaderLogo {
					color: $primaryColor;
					font-family: 'Poppins', sans-serif;
					font-size: 22px;
					font-weight: 700;
					line-height: 100%;
					display: inline-flex;
					align-items: center;
					gap: 10px;

					img {
						width: 30px;
						height: 30px;
					}
				}

				.modalHeaderClose {
					font-size: 25px;
					cursor: pointer;
				}
			}

			.modalBody {
				margin: 0 auto;
				display: flex;
				gap: 30px;

				.modalBodySidebar {
					width: 280px;
					height: 100%;
					padding-left: 30px;
					margin: 20px 0 0 0;
					overflow: hidden;

					.modalBodySidebarSearch {
						position: relative;
						padding: 5px 5px;
						margin-bottom: 8px;

						input {
							width: 100%;
							height: 35px;
							border: 1px solid #ccc;
							border-radius: 3px;
							padding: 0 10px;

							&:focus {
								outline-color: $primaryColor;
							}
						}

						svg {
							width: 34px;
							height: 34px;
							position: absolute;
							top: 50%;
							right: 6px;
							transform: translateY(-50%);
							padding: 9px;
							cursor: pointer;
						}
					}

					.modalBodySidebarTypes {
						display: flex;
						gap: 10px;
						margin: 20px 0;
					}

					.modalBodySidebarCategories {
						list-style-type: none;
						max-height: calc(80vh - 211px);
						overflow-y: auto;
						padding: 5px 15px 5px 5px;

						li {
							width: 100%;
							justify-content: start;
						}
					}
				}

				.modalBodyTemplates {
					flex: 1;
					max-height: calc(80vh - 90px);
					padding-right: 30px;
					overflow-y: auto;
					overflow-x: hidden;
					display: flex;
					flex-direction: column;
					align-items: center;

					.grid {
						width: 100%;
						display: grid;
						gap: 20px;
					}

					.modalBodyTemplateItem {
						text-align: center;
						display: flex;
						flex-direction: column;
						gap: 8px;
						margin: 10px 0;

						.modalBodyTemplateItemProBadge {
							background: $primaryColor;
							color: white;
							padding: 0 6px;
							font-size: x-small;
							border-radius: 40px;
							margin: -1px 0 0 0;
							box-shadow: 0px 0px 10px #9297b7d9;
						}

						.modalBodyTemplateItemPreviewWrap {
							position: relative;
							flex: 1;
							display: grid;
							align-items: center;

							.modalBodyTemplateItemPreview {
								border: 1px solid #ccc;
								width: auto;
								border-radius: 5px;
							}

							.modalBodyTemplateItemButton {
								position: absolute;
								top: 0;
								left: 0;
								visibility: hidden;
								opacity: 0;
								right: 0;
								bottom: 0;
								display: flex;
								align-items: center;
								justify-content: center;
								backdrop-filter: blur(1px);
								z-index: 99;
								transition: all 0.3s ease-in-out;

								a {
									font-size: 13px;
									text-decoration: none;
									color: white;
									background: $primaryColor;
									display: flex;
									align-items: center;
									justify-content: center;
									gap: 5px;
									padding: 7px 20px;
									border-radius: 4px;
									transition: all 0.3s;
									cursor: pointer;
								}

								svg {
									color: white;
								}
							}
						}

						.modalBodyTemplateItemTitle {
							display: flex;
							gap: 8px;
							justify-content: center;
							align-items: center;
						}

						&:hover {
							.modalBodyTemplateItemButton {
								visibility: visible;
								opacity: 1;
							}
						}
					}

					.modalBodyTemplateLoadMore {
						padding: 10px 20px;
						margin-top: 25px;
					}
				}

				.modalBodyTemplateNotFound {
					flex: 1;
					text-align: center;
					font-size: 24px;
					font-weight: 600;
					color: #ff6666;
					padding: 30px;
				}
			}
		}
	}
}